@import url("https://fonts.googleapis.com/css2?family=Lobster&family=Open+Sans:wght@400;600;800&display=swap");

:root {
  --maincolor: #e7a42b;
  --lightaccent: #bcaeb7;
  --lightshade: #f1edeb;
  --darkaccent: #977da3;
  --darkshade: #597a9e;
  --bordercolor: #fff;
  --sidenavbordercolor: #cf9427;
  --divider-color: #597a9e;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  /* Debug Layout lines */
  /* border: 1px solid lime; */
}

.hidden{
  display: none;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

main{
  display: flex;
  flex-direction: column;
  justify-items: flex-start;
  align-items: stretch;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  
/* ff 3.6+ */
background-image:-moz-radial-gradient(circle at 50% 50%, rgba(231, 164, 43, 0.79) 11%, rgba(89, 122, 158, 0.58) 76%);

/* safari 5.1+,chrome 10+ */
background-image:-webkit-radial-gradient(circle at 50% 50%, rgba(231, 164, 43, 0.79) 11%, rgba(89, 122, 158, 0.58) 76%);

/* opera 11.10+ */
background-image:-o-radial-gradient(circle at 50% 50%, rgba(231, 164, 43, 0.79) 11%, rgba(89, 122, 158, 0.58) 76%);

/* ie 10+ */
background-image:-ms-radial-gradient(circle at 50% 50%, rgba(231, 164, 43, 0.79) 11%, rgba(89, 122, 158, 0.58) 76%);

/* global 92%+ browsers support */
background-image:radial-gradient(circle at 50% 50%, rgba(231, 164, 43, 0.79) 11%, rgba(89, 122, 158, 0.58) 76%);

}

.page-container{
    margin: 0;
    min-height: 100vh;
    background-color: rgba(255, 255, 255, 0.85);
    text-align: center;
}

.content-container{
  padding: 2vh 5vw;
  
  @media screen and (min-width: 750px) {
    padding: 2vh 2vw;
}

}

.logo,
h1,
h2 {
  font-family: "Lobster", cursive;
  color: var(--maincolor);
}

h1 {
  font-size: 2rem; 
}

@media screen and (max-width: 340px) {
  h1{
    font-size: 1.5rem;
  }
  h2{
    font-size: 1.25rem;
  }
  p{
    font-size: 0.95rem;
  }
}

h3,
h4 {
  color: var(--darkshade);
}

* a {
  color: var(--maincolor);
  text-decoration: none;
}

.hoverable:hover {
  color: var(--darkaccent);
}

.no-padmar {
  margin: 0;
  padding: 0;
}

.side-padded {
  padding-left: 12.5%;
  padding-right: 12.5%;
}

@mixin flex-as-column {
  display: flex;
  flex-direction: column;
}

@mixin flex-as-row {
  display: flex;
  flex-direction: row;
}

.scale-up-center {
  -webkit-animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1)
    both;
  animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

@media screen and (min-width: 850px) {
  .page-container{
    margin: 0 10vw;
  }
}

